//
class Food {
    constructor() {
        //foodelement
        this.element = document.getElementById('food'); //!idfood,
    }
    //,
    //
    //XY
    //X
    get X() {
        return this.element.offsetLeft; //offsetLeft
    }
    //Y
    get Y() {
        return this.element.offsetTop; //offsetTop
    }
    //
    change() {
        //
        //0,290
        //,10,10
        let top = Math.round(Math.random() * 28) * 10;
        let left = Math.round(Math.random() * 28) * 10;
        this.element.style.left = left + 'px'; //style.DOM,
        this.element.style.top = top + 'px'; //style.leftstyle.top
    }
}
export default Food;
