//
import Snake from './snake';
import Food from './food';
import ScorePanel from './scorepanel';
class GameControl {
    constructor() {
        //()
        this.direction = '';
        //
        this.isLive = true;
        this.snake = new Snake();
        this.food = new Food();
        this.scorePanel = new ScorePanel(10, 2);
        this.init(); //
    }
    //
    init() {
        //
        document.addEventListener('keydown', this.keydownHandler.bind(this)); //bind(this)this
        this.run();
    }
    //
    keydownHandler(event) {
        //event.key()
        //direction
        this.direction = event.key; //event.keystring - 
    }
    //
    run() {
        let X = this.snake.X;
        let Y = this.snake.Y;
        //XY
        switch (this.direction) {
            case "ArrowUp":
            case "Up":
                Y -= 10;
                break;
            case "ArrowDown":
            case "Down":
                Y += 10;
                break;
            case "ArrowLeft":
            case "Left":
                X -= 10;
                break;
            case "ArrowRight":
            case "Right":
                X += 10;
                break;
        }
        //
        this.checkEat(X, Y);
        //XY - 
        try {
            this.snake.X = X;
            this.snake.Y = Y;
        }
        catch (e) {
            //catch
            alert(e.message + 'GAME OVER!');
            //isLivefalse
            this.isLive = false;
        }
        //
        this.isLive && setTimeout(this.run.bind(this), 100 - (this.scorePanel.level - 1) * 10); //; is.LivetruesetTimeout
    }
    //
    checkEat(X, Y) {
        if (X === this.food.X && Y === this.food.Y) {
            //
            this.food.change();
            //
            this.scorePanel.addScore();
            //
            this.snake.addBody();
        }
    }
}
export default GameControl;
